          <div class="scroll-wrapper-outer">
            <div class="scroll-wrapper" ref="listScroll">
              <div class="scroll-content">
                <div class="scroll-item" v-for="(item,index) in list" :key="index" @click.stop="listClick(index+1)">
                  <img :src="'0' + listIndex === item.id ? item.selectUrl : item.url" :alt="item.enName">
                </div>
              </div>
            </div>
            <div class="custom-scrollbar" ref="customScrollbar">
