                  <!-- scroll start -->
                  <div class="scroll-wrapper" ref="listContentScroll">
                    <div class="scroll-content" ref="scrollContent" @touchmove.prevent>
                      <div :style="{backgroundColor:item.id === contentIndex[item.parentId] ? '#767676' : 'white'}" class="scroll-item" v-for="(item,index) in itemOuter.children" :key="index" @click="contentChange(item.id,item.parentId)">
                        <img :src="item.url" :alt="item.enName">
                      </div>
                    </div>
                  </div>
                  <!-- scroll end -->
                  <div class="custom-scrollbar" ref="customBottomScrollbar">
