<script>
import BScroll from '@better-scroll/core'
import Scrollbar from '@better-scroll/scroll-bar'
import MouseWheel from '@better-scroll/mouse-wheel'
import Swiper from 'swiper'
import 'swiper/swiper.min.css'
import list from './datas/list.json'
import D3 from './components/D3'
import {transAllTo2String} from './utils'
const faceTotalNum = 4
const hairTotalNum = 19
const eyebrowTotalNum = 9
const eyeTotalNum = 14
const noseTotalNum = 7
const mouthTotalNum = 10
const accessTotalNum = 9
const bodyTotalNum = 5
const frameTotalNum = 7

BScroll.use(Scrollbar)
BScroll.use(MouseWheel)
export default {
  name: 'App',
  components:{
    D3
  },
  data () {
    return {
      list,
      listIndex:1,
      // face,hair,eyebrow,eye,nose,mouth,access,body,frame,
      contentIndex:{
        '01':'01',
        '02':'02',
        '03':'09',
        '04':'03',
        '05':'05',
        '06':'10',
        '07':'06',
        '08':'04',
        '09':'08'
      },
      changeIndex:{
        parentId:'',
        id:''
      },
      svgIndexObj:{
        '01':0,
        '02':1,
        '03':8,
        '04':2,
        '05':4,
        '06':9,
        '07':5,
        '08':3,
        '09':7
      },
      indexArr:[0,1,2,3,4,5,6,7,8]
    }
  },
  mounted(){
    this.bottomScroll = []
    const that = this
    this.initTop()
    this.swiperBox = new Swiper('.swiper',{
      direction : 'horizontal',
      on: {
        imagesReady: function(){
            that.initBottom()
        }, 
        slideChangeTransitionEnd: function(){
          let index = this.activeIndex
          that.$nextTick(() => {
            that.initBottom()
          })
          that.listIndex = index+1
        }
      }
    })
  },
  methods:{
    save(){
      this.$refs.d3Svg.savePhoto()
    },
    calcRandom(){
      this.faceRandomIndex = Math.round(Math.random()*faceTotalNum);
      this.hairRandomIndex = Math.round(Math.random()*hairTotalNum);
      this.eyebrowRandomIndex = Math.round(Math.random()*eyebrowTotalNum);
      this.eyeRandomIndex = Math.round(Math.random()*eyeTotalNum);
      this.noseRandomIndex = Math.round(Math.random()*noseTotalNum);
      this.mouthRandomIndex = Math.round(Math.random()*mouthTotalNum);
      this.accessRandomIndex = Math.round(Math.random()*accessTotalNum);
      this.bodyRandomIndex = Math.round(Math.random()*bodyTotalNum);
      this.frameRandomIndex = Math.round(Math.random()*frameTotalNum);
      this.svgIndexObj = {
        '01':this.faceRandomIndex,
        '02':this.hairRandomIndex,
        '03':this.eyebrowRandomIndex,
        '04':this.eyeRandomIndex,
        '05':this.noseRandomIndex,
        '06':this.mouthRandomIndex,
        '07':this.accessRandomIndex,
        '08':this.bodyRandomIndex,
        '09':this.frameRandomIndex
      }
      this.contentIndex = {
        '01':transAllTo2String(this.faceRandomIndex+1),
        '02':transAllTo2String(this.hairRandomIndex+1),
        '03':transAllTo2String(this.eyebrowRandomIndex+1),
        '04':transAllTo2String(this.eyeRandomIndex+1),
        '05':transAllTo2String(this.noseRandomIndex+1),
        '06':transAllTo2String(this.mouthRandomIndex+1),
        '07':transAllTo2String(this.accessRandomIndex+1),
        '08':transAllTo2String(this.bodyRandomIndex+1),
        '09':transAllTo2String(this.frameRandomIndex+1)
      }
    },
    initTop() {
      this.topScroll = new BScroll(this.$refs.listScroll, {
        scrollX: true,
        scrollY: false,
        click: true,
        probeType: 1,
        mouseWheel: true,
        scrollbar: {
          customElements: [this.$refs.customScrollbar],
          fade: false,
          interactive: true,
          scrollbarTrackClickable: true,
          scrollbarTrackOffsetType: 'clickedPoint' 
        }
      })
    },
    initBottom() {
      if(!this.bottomScroll[0]){
        this.indexArr.forEach((index) => {
          this.bottomScroll[index] = new BScroll(this.$refs.listContentScroll[index], {
            scrollX: false,
            scrollY: true,
            eventPassthrough: 'horizontal',
            click: false,
            probeType: 1,
            mouseWheel: true,
            scrollbar: {
              customElements: [this.$refs.customBottomScrollbar[index]],
              fade: false,
              interactive: true,
              scrollbarTrackClickable: true,
              scrollbarTrackOffsetType: 'clickedPoint' 
            }
          })
        });
      }else{
        this.bottomScroll[this.listIndex-1].refresh()
      }
    },
    listClick(index){
      this.listIndex = index
      this.swiperBox.slideTo(index -1)
    },
    contentChange(id,parentId){
      this.contentIndex[parentId] = id
      this.changeIndex.parentId = parentId
      this.changeIndex.id = id
    }
  }
}
</script>

