<template>
  <div id="faceContainer">
  </div>
</template>
<script type="text/ecmascript-6">
import * as d3 from 'd3';
import saveSvgAsPng from "save-svg-as-png"
import list from '../datas/list.json'
const svgWidth = 400;
const svgHeight = 400;

export default {
  data() {
    return {
      parentIdCode:{
        '01':this.faceChange,
        '02':this.hairChange,
        '03':this.eyebrowChange,
        '04':this.eyeChange,
        '05':this.noseChange,
        '06':this.mouthChange,
        '07':this.accessChange,
        '08':this.bodyChange,
        '09':this.frameChange
      }
    }
  },
  mounted() {
    this.initSVG()
    this.initHeadEdit()
    window.onresize = () => {
      d3.select("svg").attr("width", this.$el.clientWidth).attr("height", this.$el.clientWidth)
    }
    this.$nextTick(() => {
      this.resizeSVG()
    })
  },
  watch:{
    'changeIndex' : {
      deep:true,
      handler : function (value) {
        const {parentId,id} = value
        let changeFun = this.parentIdCode[parentId]
        changeFun(id*1-1)
      }
    },
    'svgIndexObj' : {
      deep:true,
      handler : function (value) {
        const svgObj = value
        for(let svgNum in svgObj){
          this.parentIdCode[svgNum](svgObj[svgNum])
        }
      }
    }
  },
  props:{
    changeIndex:{
      type:Object,
    },
    svgIndexObj:{
      type:Object,
    }
  },
  methods: {
    savePhoto(){
      let el = document.getElementsByTagName("svg")[0];
      saveSvgAsPng.saveSvgAsPng(el, "myAvatar.png");
    },
    initSVG(){
      this.svg = d3.select(this.$el)
        .append('svg')
        .attr("xmlns", "http://www.w3.org/2000/svg")
        .attr("version", "1.1")
        .attr("id", "contentSVG")
        .attr('width', svgWidth)
        .attr('height', svgHeight)
        .attr("viewBox", "0 0 " + svgWidth + " " + svgHeight)
        .attr("xml:space", "preserve");
      this.frameBackSVG = this.svg.append("g").attr("id", "frame-back");
      this.personSVG = this.svg.append("g").attr("id", "person");
      this.bodySVG = this.personSVG.append("g").attr("id", "body");
      this.faceSVG = this.personSVG.append("g").attr("id", "face");
      this.accessSVG = this.personSVG.append("g").attr("id", "access");
      this.noseSVG = this.personSVG.append("g").attr("id", "nose");
      this.eyeSVG = this.personSVG.append("g").attr("id", "eye");
      this.mouthSVG = this.personSVG.append("g").attr("id", "mouth");
      this.eyebrowSVG = this.personSVG.append("g").attr("id", "eyebrow");
      this.hairSVG = this.personSVG.append("g").attr("id", "hair");
      this.frameSVG = this.svg.append("g").attr("id", "frame");
    },
    initHeadEdit(){
      this.faceChange(0)
      this.hairChange(1)
      this.eyebrowChange(8)
      this.eyeChange(2)
      this.noseChange(4)
      this.mouthChange(9)
      this.accessChange(5)
      this.bodyChange(3)
      this.frameChange(7)
    },
    faceChange(index){
      this.expressDataParse(this.faceSVG,"facePart",list[0].children[index],true)
    },
    hairChange(index){
      this.expressDataParse(this.hairSVG,"hairPart",list[1].children[index],true)
    },
    eyebrowChange(index){
      this.expressDataParse(this.eyebrowSVG,"eyebrowPart",list[2].children[index],true)
    },
    eyeChange(index){
      this.expressDataParse(this.eyeSVG,"eyePart",list[3].children[index],true)
    },
    noseChange(index){
      this.expressDataParse(this.noseSVG,"nosePart",list[4].children[index],true)
    },
    mouthChange(index){
      this.expressDataParse(this.mouthSVG,"mouthPart",list[5].children[index],true)
    },
    accessChange(index){
      this.expressDataParse(this.accessSVG,"accessPart",list[6].children[index],true)
    },
    bodyChange(index){
      this.expressDataParse(this.bodySVG,"bodyPart",list[7].children[index],true)
    },
    frameChange(index){
      if(index>=4){
        this.expressDataParse(this.frameBackSVG,"frameBackPart",list[8].frameBack[1],true)
      }else{
        this.expressDataParse(this.frameBackSVG,"frameBackPart",list[8].frameBack[0],true)
      }
      this.expressDataParse(this.frameSVG,"framePart",list[8].children[index],true)
    },
    // OrganChange Start
    expressDataParse(partSVG, partClass, data, tag){
      if (tag) { d3.selectAll("." + partClass).remove() }
      if (data.svg.path.length > 0) { 
        partSVG.selectAll("." + partClass)
          .data(data.svg.path).enter()
          .append("g")
          .attr("class", function(g) {
            var itemSVG = d3.select(this);
            var itemSVGPart = itemSVG.append(g.type);
            g.style.forEach(function(h) { 
              itemSVGPart.attr(h.attr, h.val) 
            });
            return partClass
          });
        if ((data.svg.cX != 0) || (data.svg.cY != 0)) { 
          partSVG.attr("transform", "translate(" + data.svg.cX + "," + data.svg.cY + ") !important") 
        }  
      }
    },
    // OrganChange End
    resizeSVG(){
      d3.select("svg").attr("width", this.$el.clientWidth).attr("height", this.$el.clientWidth)
    }
  },
}
</script>
<style lang="scss">
  .faceContainer{
    height: 100%;
    width: 100%;
  }
</style>